import React, { useEffect } from 'react'
import { connect } from 'react-redux'

import ListDialog from './Lists/ListDialog'
import PlanEditor from './PlanEditor'
import PlansTable from './PlansTable'
import PageHeader from '../PageHeader'
import Spinner from '../Spinner'

import {
  PLANS_PAGE_LOADED,
  PLANS_PAGE_UNLOADED,
} from '../../constants/actionTypes'
import agent from '../../agent'

const mapStateToProps = (state) => ({
  pageLoaded: state.plans.pageLoaded,
})

const mapDispatchToProps = (dispatch) => ({
  onLoad: () =>
    dispatch({
      type: PLANS_PAGE_LOADED,
      payload: Promise.all([
        agent.Plans.getAll(),
        agent.Meals.getAll(),
        agent.Recipes.getAll(),
        agent.Ingredients.getAll(),
        agent.Categories.getAll(),
        agent.Stores.getAll(),
      ]),
    }),
  onUnload: () => dispatch({ type: PLANS_PAGE_UNLOADED }),
})

const Plans = ({ onLoad, onUnload, pageLoaded }) => {
  useEffect(() => {
    onLoad()

    return () => {
      onUnload()
    }
  }, [])

  return (
    <>
      <PageHeader title='Manage Plans'>
        <PlanEditor />
      </PageHeader>
      {pageLoaded ? <PlansTable /> : <Spinner />}
      <ListDialog />
    </>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(Plans)
